import React from 'react';

const BudgetTable = ({ items = [], currency, onDelete }) => {
    return (
        <div className="table-responsive">
            <table className="table table-striped mx-auto">
                <thead>
                    <tr>
                        <th>Description</th>
                        <th>Amount ({currency})</th>
                        <th>Date/Time</th> {/* Updated header to show date and time */}
                        <th>Action</th>
                    </tr>
                </thead>
                <tbody>
                    {items.map((item) => (
                        <tr key={item.id}>
                            <td>{item.description || "N/A"}</td> {/* Display description or "N/A" */}
                            <td>{currency} {item.amount.toFixed(2)}</td>
                            <td>{item.dateTime}</td> {/* Displaying date and time */}
                            <td>
                                <button 
                                    className="btn btn-danger btn-sm" 
                                    onClick={() => onDelete(item.id)}
                                >
                                    Delete
                                </button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default BudgetTable; // Ensure correct export of the component