import React, { useState, useEffect } from 'react';
import './App.css';
import BudgetTable from './BudgetTable';
import jsPDF from 'jspdf';
import * as XLSX from 'xlsx';
import 'bootstrap/dist/css/bootstrap.min.css';
import '@fortawesome/fontawesome-free/css/all.min.css';

function App() {
    const [sessions, setSessions] = useState({});
    const [budgetSession, setBudgetSession] = useState('');
    const [selectedSession, setSelectedSession] = useState(''); 
    const [description, setDescription] = useState('');
    const [amount, setAmount] = useState('');
    const [initialBudget, setInitialBudget] = useState('');
    const [currency, setCurrency] = useState('USD');
    const [currentTime, setCurrentTime] = useState(new Date());
    const [currentDate, setCurrentDate] = useState(new Date().toLocaleDateString());

    // Update time every second
    useEffect(() => {
        const timer = setInterval(() => {
            setCurrentTime(new Date());
            setCurrentDate(new Date().toLocaleDateString());
        }, 1000);
        
        return () => clearInterval(timer); // Clear interval on unmount
    }, []);

    // Function to add a new budget session
    const addSession = () => {
        if (!budgetSession || sessions[budgetSession]) {
            alert("Please enter a unique name for the budget session.");
            return;
        }

        const newSessions = {
            ...sessions,
            [budgetSession]: { items: [], initialBudget: parseFloat(initialBudget), remainingBudget: parseFloat(initialBudget) }
        };

        setSessions(newSessions);
        setSelectedSession(budgetSession); // Automatically select the new session
        setBudgetSession('');
        setInitialBudget('');
    };

    // Function to delete a session
    const deleteSession = () => {
        if (!selectedSession) return;

        const updatedSessions = { ...sessions };
        delete updatedSessions[selectedSession]; // Remove the selected session

        setSessions(updatedSessions);
        setSelectedSession(''); // Clear the selected session
        alert("Session deleted successfully.");
    };

    // Function to add an item to the selected session
    const addItem = (e) => {
        e.preventDefault();
        if (!selectedSession || !sessions[selectedSession]) {
            alert("Please select a budget session.");
            return;
        }

        const newItemAmount = parseFloat(amount);
        const updatedSession = { ...sessions[selectedSession] };

        // Check if the transaction would cause remaining budget to go negative
        if (newItemAmount > updatedSession.remainingBudget) {
            alert("Transaction exceeds remaining budget. Please enter a smaller amount.");
            return; // Stop if the budget would become negative
        }

        const newItem = {
            id: Date.now(),
            description: description || "No description", // Optional description
            amount: newItemAmount,
            dateTime: currentTime.toLocaleString(), // Store current date and time
        };

        updatedSession.items = [...updatedSession.items, newItem];
        updatedSession.remainingBudget -= newItemAmount;

        setSessions({ ...sessions, [selectedSession]: updatedSession });
        setDescription('');
        setAmount('');
    };

    // Function to delete an item from the selected session
    const deleteItem = (id) => {
        const updatedSession = { ...sessions[selectedSession] };
        const itemToDelete = updatedSession.items.find((item) => item.id === id);

        updatedSession.items = updatedSession.items.filter((item) => item.id !== id);
        updatedSession.remainingBudget += itemToDelete.amount;

        setSessions({ ...sessions, [selectedSession]: updatedSession });
    };

    // Function to export transactions to PDF
    const savePDF = () => {
        const doc = new jsPDF();
        doc.text("Budget Tracker", 20, 20);
        sessions[selectedSession]?.items.forEach((item, index) => {
            doc.text(`${index + 1}. ${item.description || "N/A"}: ${currency} ${item.amount.toFixed(2)} - ${item.dateTime}`, 20, 30 + (index * 10));
        });
        doc.save('budget-tracker.pdf');
    };

    // Function to export transactions to Excel
    const exportToExcel = () => {
        const worksheet = XLSX.utils.json_to_sheet(sessions[selectedSession]?.items.map(item => ({
            Description: item.description || "N/A",
            Amount: `${currency} ${item.amount.toFixed(2)}`,
            DateTime: item.dateTime, // Include date and time
        })));
        const workbook = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(workbook, worksheet, "Budget Tracker");
        XLSX.writeFile(workbook, "budget_tracker.xlsx");
    };

    return (
        <div className="container mt-5">
            <h1 className="text-center">Budget Tracker</h1>

            {/* Digital clock in the top left corner */}
            <div className="text-left">
                <h5>{currentTime.toLocaleTimeString()}</h5>
            </div>

            {/* Digital calendar in the top right corner */}
            <div className="text-right">
                <h5>{currentDate}</h5>
            </div>

            {/* Set initial budget and create a new session */}
            <div className="text-center mb-4">
                <label htmlFor="initialBudget" className="font-weight-bold text-uppercase">Set Initial Budget:</label>
                <div className="input-group mb-3 justify-content-center">
                    <input
                        type="number"
                        id="initialBudget"
                        className="form-control w-25"
                        value={initialBudget}
                        onChange={(e) => setInitialBudget(e.target.value)}
                        required
                    />
                    <select 
                        className="custom-select w-25 mx-2" 
                        value={currency} 
                        onChange={(e) => setCurrency(e.target.value)}
                    >
                        <option value="USD">Dollar (USD)</option>
                        <option value="EUR">Euro (EUR)</option>
                        <option value="OMR">Omani Rial (OMR)</option>
                    </select>
                    <input
                        type="text"
                        placeholder="New session name"
                        value={budgetSession}
                        onChange={(e) => setBudgetSession(e.target.value)}
                        className="form-control w-25 mx-2"
                    />
                    <button 
                        onClick={addSession}
                        className="btn btn-primary ml-2"
                    >
                        Create Session
                    </button>
                </div>
            </div>

            {/* Display initial and remaining budget */}
            {selectedSession && sessions[selectedSession] && (
                <div className="text-center mb-3">
                    <h4>Initial Budget: {currency} {sessions[selectedSession].initialBudget.toFixed(2)}</h4>
                    <h4>Remaining Budget: {currency} {sessions[selectedSession].remainingBudget.toFixed(2)}</h4>
                </div>
            )}

            {/* Select session */}
            <div className="text-center mb-4">
                <label>Select Budget Session:</label>
                <div className="input-group mb-3 justify-content-center">
                    <select
                        className="custom-select w-50"
                        value={selectedSession}
                        onChange={(e) => setSelectedSession(e.target.value)}
                    >
                        <option value="">Select a session</option>
                        {Object.keys(sessions).map((session) => (
                            <option key={session} value={session}>{session}</option>
                        ))}
                    </select>
                    <button 
                        onClick={deleteSession} 
                        className="btn btn-danger ml-2"
                        disabled={!selectedSession}
                    >
                        Delete Session
                    </button>
                </div>
            </div>

            <form onSubmit={addItem} className="mb-4">
                <div className="input-group mb-3">
                    <input
                        type="number"
                        className="form-control"
                        placeholder="Amount"
                        value={amount}
                        onChange={(e) => setAmount(e.target.value)}
                        required
                    />
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Description (optional)"
                        value={description}
                        onChange={(e) => setDescription(e.target.value)}
                    />
                    <button type="submit" className="btn btn-primary">Add</button>
                </div>
            </form>

            <div className="text-center mb-3">
                <button type="button" className="btn btn-success mx-2" onClick={savePDF}>
                    <i className="fas fa-file-pdf"></i> Save as PDF
                </button>
                <button type="button" className="btn btn-info mx-2" onClick={exportToExcel}>
                    <i className="fas fa-file-excel"></i> Export to Excel
                </button>
            </div>

            {/* Display transactions only for the selected session */}
            <BudgetTable items={sessions[selectedSession]?.items || []} currency={currency} />
        </div>
    );
}

export default App;